.ContactList__list {
    padding: 20px 20px;
    max-width: 480px;
  }
  
  .ContactList__item {
    padding: 5px;
    display: flex;
    border-bottom: 1px solid #999;
  }
  
  .ContactList__text {
    margin-right: auto;
    line-height: 34px;
  }
  
  .ContactList__button {
    width: 100px;
    height: 32px;
    color: white;
    background-color: #2f8d92fd;
    font-size: 16px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color ease-in-out 300ms;
  }
  
  .ContactList__button:hover {
    background-color: #03e9f4;
  }
